.nav {
    position: relative;

    @include sm-block() {
        display: none;
    }

    &__mob {
        position: absolute;
        z-index: 98;
        display: none;
        width: 100%;
        margin: 0 0 30px;
        padding: 30px 0;
        background-color: #fff;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.09);

        &.on {
            display: flex;
        }

        &_menu {
            width: 100%;
        }

        ul {
            padding: 0 25px;

            li {
                width: 100%;
                padding: 15px 0;

                &.on ul.sub-menu {
                    display: block;
                }

                &.menu-item-has-children {
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        top: 18px;
                        right: 16px;
                        width: 16px;
                        max-width: 16px;
                        height: 16px;
                        max-height: 16px;
                        transform: rotate(90deg);
                        transition: all 0.3s ease;
                        background-image: url('data:image/svg+xml;utf8;base64,');
                        background-size: cover;
                    }

                    &.on {
                        position: relative;

                        &::before {
                            transform: rotate(-90deg);
                        }
                    }
                }

                ul.sub-menu {
                    display: none;
                    margin: 15px -25px 0;
                    background: rgba(244, 244, 244, 0.7);

                    li {
                        display: inline-block;
                        margin: 0 25px;
                        padding: 15px 0;
                        border-top: 1px solid rgba(196, 196, 196, 0.2);

                        &:first-child {
                            border-top: transparent;
                        }
                    }
                }

                a {
                    padding: 0;
                    color: $color-text;
                }
            }
        }
    }

    ul {
        margin: 0;
        padding: 0;
        border-radius: 5px;
        background: #f7f7f7;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            padding: 0;

            &:hover ul {
                display: block;
            }

            &.current_page_item::after,
            &.current_page_parent::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                width: 10px;
                height: 5px;
                transform: translate(-50%, 0);
                border-radius: 5px 5px 0 0;
                background: #2f84e3;
            }

            a {
                display: block;
                padding: 17px 20px;
                transition: all 0.3s ease;
                color: #000;
                text-decoration: none;
                text-transform: uppercase;

                @include lg-block() {
                    padding: 17px 15px;
                    font-size: 14px;
                }

                @include md-block() {
                    padding: 17px 10px;
                    font-size: 14px;
                }

                &:hover {
                    color: $color-accent;
                }
            }

            ul {
                position: absolute;
                top: 100%;
                z-index: 99;
                display: none;
                border-top: 1px solid #2f84e3;
                border-radius: 0;
                box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.1);
                text-align: left;

                li {
                    width: 100%;
                    transition: all 0.3s ease;
                    border-top: 1px solid rgba(0, 0, 0, 0.1);

                    &:hover {
                        background: rgba(0, 0, 0, 0.02);
                    }

                    a {
                        display: block;
                        font-size: 14px;
                        text-transform: none;
                    }
                }
            }
        }
    }
} // .nav
