.header {
    position: relative;
    margin: 24px 0;

    @include sm-block() {
        display: none;
    }

    &__mob {
        position: relative;
        z-index: 99;
        display: none;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 15px;
        background: #fff;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.03);

        @include sm-block() {
            display: flex;
        }

        &_phone img {
            width: 32px;
            height: auto;
        }

        &_logo img {
            width: 90px;
            height: auto;
        }

        &_btn {
            width: 32px;
            height: 32px;
        }

        .hamburger__menu,
        .hamburger__menu::after,
        .hamburger__menu::before {
            width: 32px;
            height: 2px;
            border-radius: 2px;
        }

        .hamburger__menu {
            position: relative;
            transform: translateY(15px);
            transition: all 0ms 300ms;
            background-color: #3482b7;

            &::before {
                content: '';
                position: absolute;
                bottom: 10px;
                left: 0;
                transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
                    transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
                background: #3482b7;
            }

            &::after {
                content: '';
                position: absolute;
                top: 10px;
                left: 0;
                transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
                    transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
                background: #3482b7;
            }

            &.animate {
                background: rgba(255, 255, 255, 0);

                &::after {
                    top: 0;
                    transform: rotate(45deg);
                    transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1),
                        transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
                }

                &::before {
                    bottom: 0;
                    transform: rotate(-45deg);
                    transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1),
                        transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
                }
            }
        }
    }

    &__avatar {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &:hover {
            position: relative;

            .header__avatar_nav_wrap {
                display: block !important;
            }
        }

        &_img {
            width: 36px;
            height: 36px;
            border-radius: 18px;
            background-color: #ccc;

            img {
                width: 100%;
                height: auto;
                border-radius: 18px;
            }
        }

        & a span {
            color: #333;
        }

        & span {
            font-size: 14px;
        }

        &_nav {
            position: absolute;
            top: 100%;
            left: 50%;
            z-index: 90;
            width: 230px;
            margin-top: 15px;
            padding: 20px;
            transform: translate(-50%, 0);
            border-radius: 10px;
            background: #fff;
            box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.1);

            &::before {
                content: '';
                position: absolute;
                bottom: 100%;
                left: 50%;
                width: 0;
                height: 0;
                transform: translateX(-50%);
                border-width: 0 6.5px 11px;
                border-style: solid;
                border-color: transparent transparent #fff;
            }

            ul {
                padding: 0;

                ul {
                    margin: 10px 0;
                    padding: 10px 0;
                    border-top: 1px solid rgba(0, 0, 0, 0.1);
                    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

                    li {
                        padding: 10px 0;
                    }
                }

                li {
                    padding: 10px 0;

                    a {
                        color: #333;
                        font-size: 16px;
                        line-height: 15px;

                        &:hover {
                            color: $color-accent;
                        }
                    }
                }
            }
        }
    }

    &__cart {
        position: relative;
        margin-top: -5px;
        text-decoration: none;

        .cart {
            display: flex;
            align-items: center;
        }

        img {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }

        span {
            color: rgba(0, 0, 0, 0.7);
            font-size: 14px;
            font-weight: normal;
            line-height: normal;
        }

        .cart_ob {
            color: rgba(0, 0, 0, 0.5);
            font-size: 14px;
            line-height: normal;

            strong {
                color: rgba(0, 0, 0, 0.7);
                font-weight: bold;
            }
        }
    }

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .logo {
        position: relative;
        display: inline-block;

        &_desc {
            display: none;
            margin-left: 26px;
            padding-left: 26px;
            border-left: 1px solid rgba(0, 0, 0, 0.05);
            color: rgba(0, 0, 0, 0.5);
            font-size: 14px;
            line-height: 1.6;

            @include lg-block() {
                padding-left: 26px;
            }

            @include md-block() {
                display: none;
            }
        }

        img {
            width: 207px;
            height: auto;
        }
    }

    .phone {
        position: relative;

        a {
            display: flex;
            align-items: center;
            color: rgba(0, 0, 0, 0.7);
            text-decoration: none;
        }

        img {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }

        .number {
            color: rgba(0, 0, 0, 0.7);
            font-size: 20px;
            line-height: normal;
        }

        span {
            display: block;
            margin-top: 3px;
            color: rgba(0, 0, 0, 0.5);
            font-size: 12px;
            line-height: normal;
        }
    }

    .email {
        position: relative;

        @include md-block() {
            display: none;
        }

        a {
            display: flex;
            align-items: center;
            color: rgba(0, 0, 0, 0.7);
            text-decoration: none;
        }

        img {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }

        .number {
            color: rgba(0, 0, 0, 0.7);
            font-size: 20px;
            line-height: normal;
        }

        span {
            display: block;
            margin-top: 3px;
            color: rgba(0, 0, 0, 0.5);
            font-size: 12px;
            line-height: normal;
        }
    }
} //.header
