.footer {
    padding: 50px 0 30px;

    &__title {
        margin-bottom: 30px;
        color: rgba(0, 0, 0, 0.3);
        font-family: $f-bold;
        font-size: 16px;
        line-height: normal;
    }

    &__pay {
        display: block;
        margin-bottom: 25px;
        color: rgba(0, 0, 0, 0.7);
        font-size: 14px;
        line-height: normal;

        &_item {
            margin-bottom: 25px;
            color: rgba(0, 0, 0, 0.7);
            font-size: 14px;
            line-height: normal;

            &_icon {
                display: flex;
                align-items: center;
                justify-content: flex-start;

                img {
                    width: 52px;
                    height: auto;
                    margin-left: 15px;

                    @include xs-block() {
                    }

                    &:first-child {
                        margin-left: 0px;
                    }
                }
            }
        }

        span {
            display: block;
            margin-bottom: 10px;
            color: rgba(0, 0, 0, 0.4);
            font-size: 12px;
            line-height: normal;
        }
    }

    &__bottom {
        margin-top: 0px;
        padding-top: 30px;
        border-top: 1px solid rgba(0, 0, 0, 0.05);
    }

    &__social {
        display: flex;
        justify-content: space-around;

        @include xs-block() {
            justify-content: center;
            margin: 20px 0 30px;
        }

        &_item {
            position: relative;

            @include xs-block() {
                margin: 0 15px;
            }

            img {
                width: 32px;
                height: 32px;
            }
        }
    }

    &__copy,
    &__copy a,
    &__dev,
    &__dev a {
        margin: 8px 0;
        transition: all 0.3s ease;
        color: rgba(0, 0, 0, 0.4);
        font-size: 14px;
        font-weight: normal;
        line-height: normal;
        text-decoration: none;

        @include xs-block() {
            text-align: center;
        }
    }

    &__copy a:hover,
    &__dev a:hover {
        color: #000;
    }

    &__copy a {
        margin-left: 10px;
    }

    &__dev {
        text-align: right;

        strong {
            font-family: $f;
        }
    }

    & ul {
        margin: 0 0 50px;
        padding: 0;
        list-style: none;

        li {
            margin-bottom: 5px;

            &.icon a {
                display: flex;
                align-items: center;
                margin-top: 15px;

                @include xs-block() {
                    margin-top: 0;
                }

                img {
                    width: 28px;
                    height: 28px;
                    margin-right: 10px;
                    @include xs-block() {
                        display: none;
                    }
                }
            }

            a {
                color: rgba(0, 0, 0, 0.7);
                font-size: 14px;
                line-height: 1.8;

                @include xs-block() {
                    font-size: 12px;
                }

                &:hover {
                    color: $color-accent;
                }
            }
        }
    }
} // .footer
